import Vue from 'vue'
import Router from 'vue-router'
import inside from '@/components/inside'
import among from '@/components/among'
import about from '@/components/about'
import experience from '@/components/experience'
import education from '@/components/education'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: "index",
      component: about //界面开始进来的时候，默认为inside界面
    },
    {
      path: '/inside',
      name: 'inside',
      component: inside,
      redirect: '/inside/experience',//进来的时候跳转到这个位置
      children: [//进行路由之间的嵌套，这是一个子路由，默认进入第一个路由
        {
          path: '/inside/experience',
          name: 'experience',
          component: experience
        },
        {
          path: '/inside/education',
          name: 'education',
          component: education
        }
      ]
    },
    {
      path: '/among',
      name: 'among',
      component: among
    },
    {
      path: '/about',
      name: 'about',
      component: about
    }
  ]
})


