        series: [
          //调色盘
          {
            name: "平均最小工资",
            type: "bar",
            data: []
          },
          {
            name: "平均最大工资",
            type: "bar",
            data: [],
            barCategoryGap: "40%"
          }
        ]
      };
      this.$http.get("api1/selectCsjiajia12").then(res => {
        if (res.status == 200) {
          let respose = res.body;
          console.log("data:", respose);
          option.xAxis[0].data = [];
          option.series[0].data = [];
          option.series[1].data = [];
          respose.map(ele => {
            option.xAxis[0].data.push(ele.education);
