<template>
  <div class="among">
    <!-- <div>
      I am among!
      调用接口：
      //以下接口是实现：查询六岗位之间的平均工资
      39.96.60.49:8080/selectAvgWages6
      //以下接口是实现：相同工作经验不同职位信息薪水比较（2年、4年、6年、不限）
      39.96.60.49:8080/selectExperienceZhiwei
      //以下几口是实现：相同学历信息不同职位信息薪水比较（大专、本科、研究生、不限）
      39.96.60.49:8080/selectEducationZhiwei23
    </div>-->

    <div>
      <p style="font-size:18px;color:#009933">六岗位之间的平均工资</p>
    </div>
    <div id="selectAvgWages6" style="width : auto; height : 250px"></div>
    <div>
      <p
        style="font-size:18px"
      >&ensp;&ensp;&ensp;&ensp;由以上信息可以看出，Python、Java和C++的平均薪资较高。由此也验证Python近几年火热的态势，由于AI的火热，Python的需求越来越大，传统的Java也比较强势，薪资待遇较高，而作为老牌的优秀编程语言的C++也比较强势。</p>
    </div>

    <hr>
    <br>

    <div>
      <p style="font-size:18px;color:#009933">相同工作经验不同职位信息薪水比较（2年、4年、6年、不限）</p>
    </div>
    <div id="selectExperienceZhiwei" style="width : auto; height : 250px"></div>
    <div>
      <p
        style="font-size:18px"
      >&ensp;&ensp;&ensp;&ensp;由以上信息可以看出，相同经验下Python、Java和C++的平均薪资较高。每个职位的薪资都会随着工作经验的增长而增长。</p>
    </div>

    <hr>
    <br>

    <div>
      <p style="font-size:18px;color:#009933">相同学历信息不同职位信息薪水比较（大专、本科、硕士、不限）</p>
    </div>
    <div id="selectEducationZhiwei23" style="width : auto; height : 250px"></div>
    <div>
      <p
        style="font-size:18px"
      >&ensp;&ensp;&ensp;&ensp;由以上信息可以看出，学历越高，薪资也会越高。当学历越高的时候，Python、Java的薪资就会比其他岗位高，说明使用Python、Java更能实现人工智能等新兴领域的研究。</p>
    </div>
    <br>
    <!-- 用于最下面 -->
    <div style="text-align: center">
      <button style="border: none;background-color: #4CAF50;border-radius: 8px;">
        <a href="javascript:scrollTo(0,0)" style="font-size: 18px;color: black;">&ensp;↑&ensp;返回到顶部</a>
      </button>
      <br>
      <br>
      <br>
    </div>
  </div>
</template>

<script type="text/ecmasript-6">
export default {
  name: "hello",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  mounted() {
    this.draw_selectAvgWages6();
    this.draw_selectExperienceZhiwei();
    this.draw_selectEducationZhiwei23();
  },
  methods: {
    draw_selectAvgWages6() {
      //写方法
      // 基于准备好的dom，初始化echarts实例
      let selectAvgWages6 = this.$echarts.init(
        document.getElementById("selectAvgWages6")
      );
      //图标数据
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          data: ["平均最小工资", "平均最大工资"]
        },
        grid: {
          left: "15%"
        },
        xAxis: [
          {
            type: "category",
            name: "工作年限",
            data: [],
            axisPointer: {
              type: "shadow"
            },
            axisLabel: {
              interval: 0
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "工资（单位：千元）",
            min: 0,
            interval: 5,
            axisLabel: {
              formatter: "{value}"
            }
          }
        ],
        series: [
          {
            name: "平均最小工资",
            type: "bar",
            data: []
          },
          {
            name: "平均最大工资",
            type: "bar",
            data: [],
            barCategoryGap: "40%"
          }
        ]
      };
      this.$http.get("api1/selectAvgWages6").then(res => {
        if (res.status == 200) {
          let respose = res.body;
          console.log("data:", respose);
          option.xAxis[0].data = [];
          option.series[0].data = [];
          option.series[1].data = [];
          respose.map(ele => {
            option.xAxis[0].data.push(ele.zhiwei);
            option.series[0].data.push(ele.avgWagesMin);
            option.series[1].data.push(ele.avgWagesMax);
          });
          console.log("data:", option);
          // 绘制图表
          selectAvgWages6.setOption(option);
        }
      });
    },
    draw_selectExperienceZhiwei() {
      //写方法
      // 基于准备好的dom，初始化echarts实例
      let selectExperienceZhiwei = this.$echarts.init(
        document.getElementById("selectExperienceZhiwei")
      );
      //图标数据
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          data: ["平均最小工资", "平均最大工资"]
        },
        grid: {
          left: "15%"
        },
        xAxis: [
          {
            type: "category",
            name: "工作年限",
            data: [],
            axisPointer: {
              type: "shadow"
            },
            axisLabel: {
              interval: 0
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "工资（单位：千元）",
            min: 0,
            interval: 5,
            axisLabel: {
              formatter: "{value}"
            }
          }
        ],
        series: [
          {
            name: "平均最小工资",
            type: "bar",
            data: []
          },
          {
            name: "平均最大工资",
            type: "bar",
            data: [],
            barCategoryGap: "40%"
          }
        ]
      };
      this.$http.get("api1/selectExperienceZhiwei").then(res => {
        if (res.status == 200) {
          let respose = res.body;
          console.log("data:", respose);
          option.xAxis[0].data = [];
          option.series[0].data = [];
          option.series[1].data = [];
          respose.map(ele => {
            option.xAxis[0].data.push(`${ele.experience}-${ele.zhiwei}`);
            option.series[0].data.push(ele.avgWagesMin);
            option.series[1].data.push(ele.avgWagesMax);
          });
          console.log("data:", option);
          // 绘制图表
          selectExperienceZhiwei.setOption(option);
        }
      });
    },
    draw_selectEducationZhiwei23() {
      //写方法
      // 基于准备好的dom，初始化echarts实例
      let selectEducationZhiwei23 = this.$echarts.init(
        document.getElementById("selectEducationZhiwei23")
      );
      //图标数据
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          data: ["平均最小工资", "平均最大工资"]
        },
        grid: {
          left: "15%"
        },
        xAxis: [
          {
            type: "category",
            name: "工作年限",
            data: [],
            axisPointer: {
              type: "shadow"
            },
            axisLabel: {
              interval: 0
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "工资（单位：千元）",
            min: 0,
            interval: 5,
            axisLabel: {
              formatter: "{value}"
            }
          }
        ],
        series: [
          {
            name: "平均最小工资",
            type: "bar",
            data: []
          },
          {
            name: "平均最大工资",
            type: "bar",
            data: [],
            barCategoryGap: "40%"
          }
        ]
      };
      this.$http.get("api1/selectEducationZhiwei23").then(res => {
        if (res.status == 200) {
          let respose = res.body;
          console.log("data:", respose);
          option.xAxis[0].data = [];
          option.series[0].data = [];
          option.series[1].data = [];
          respose.map(ele => {
            option.xAxis[0].data.push(`${ele.education}-${ele.zhiwei}`);
            option.series[0].data.push(ele.avgWagesMin);
            option.series[1].data.push(ele.avgWagesMax);
          });
          console.log("data:", option);
          // 绘制图表
          selectEducationZhiwei23.setOption(option);
        }
      });
    }
  }
};
</script>

<style lang='stylus' rel='stylessheet/stylus'>
.among {
  background-color: #E8E8E8;
  width: 98%;
  margin: 0 auto;
}
</style>


